  font-family: "Young Style", sans-serif;
  font-family: "Young Style Underlines", sans-serif;
  src: url("https://fonts.cdnfonts.com/css/young-style") format("woff2");
}
#app {
  display: flex;
  flex-direction: column;
}
#app #title, #app #name {
  font-family: "Young Style", sans-serif;
  text-align: center;
  color: #4a3f35;
}
#app #about {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
#app #about #about-txt {
  width: 90%;
  padding: 20px;
  font-family: "Courier New", Courier, monospace;
}
#app #about #located {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-left: 100px;
}
#app #about #located #aside {
  display: flex;
  flex-direction: column;
  align-items: center;
}
#app #about #located #aside #bottles {
  height: 300px;
  width: 500px;
  border: ridge #4a3f35 5px;
}
#app #about #located #aside #map-txt {
  height: 90%;
  width: 90%;
  padding: 20px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}
#app #about #display {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
#app #about #display .photos {
  width: 40%;
}
#app #about #display #tags {
  margin-top: -30px;
  color: #4a3f35;
  padding: 20px;
  margin-left: -300px;
}
#app #about #display #tags #name {
  font-size: 40px;
  margin-bottom: -20px;
}
#app #about #display #tags p {
  font-family: "Courier New", Courier, monospace;
}
#app #about #display #house {
  border: ridge #4a3f35 5px;
  margin-right: -150px;
  margin-left: 40px;
